html {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    background: #fff;
    color: #000;
    letter-spacing: normal;
    overflow-x: hidden;



    @media screen and (max-width: $w1440) {
        font-size: 15px;
    }

    @media screen and (max-width: $w1376) {
        font-size: 14px;
    }

    @media screen and (max-width: $w1280) {
        font-size: 12px;
    }
}

[class*="ymaps-2-1"][class*="-ground-pane"] {
    filter: grayscale(1);
}

.swiper-button-disabled {
    opacity: 0.3 !important;
}

.perf-no-animation * {
    transition: none !important;
    animation: none !important;
}

.swiper-container:not(.swiper-initialized) {
    display: none;
}

button:active,
button:hover,
input:active,
input:focus,
input:hover {
    outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
    line-height: 130%;
}

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
    color: #000;
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
}

ul {
    list-style-type: none;
}

.hidden {
    overflow: hidden;

    @media screen and (min-width: $w1024) {
        margin-right: 17px;
    }
}

.hide {
    display: none;
}

.rotate {
    transform: rotate(180deg);
}

section {
    overflow: hidden;
    position: relative;

}

main {
    width: 100%;
}

html.with-fancybox {
    overflow: hidden !important;
}

.snipet-load:not(.loaded)::before {
    position: absolute;
    width: 30px;
    height: 30px;
    content: '';
    border: 2px solid #0B6D7A;
    border-radius: 50%;
    border-left-color: transparent;
    animation: rotate 0.5s linear infinite;
    left: 50%;
    top: 50%;
    margin-left: -15px;
    margin-top: -15px;
}

.container {
    max-width: 1520px;
    width: 100%;
    margin: 0 auto;
    padding: 0 rem(30);
    box-sizing: border-box;

    @media screen and (max-width: $w1440) {
        max-width: 1300px;
    }

    @media screen and (max-width: $w1376) {
        max-width: 1100px;
    }

    @media screen and (max-width: $w940) {
        padding: 0 rem(30);
        max-width: 730px;
    }

    @media screen and (max-width: $w760) {
        max-width: 580px;
    }

    @media screen and (max-width: $w480) {
        padding: 0 rem(15);
        max-width: 360px;
    }
}

.pull-right {
    justify-content: flex-end !important;
}

.pull-left {
    justify-content: flex-start !important;
}

::-webkit-input-placeholder {
    /* Chrome */
    color: $gray;
}

:-ms-input-placeholder {
    /* IE 10+ */
    color: $gray;
}

::-moz-placeholder {
    /* Firefox 19+ */
    color: $gray;
    opacity: 1;
}

:-moz-placeholder {
    /* Firefox 4 - 18 */
    color: $gray;
    opacity: 1;
}

.gmnoprint {
    opacity: 0;
}

.gm-style-cc {
    opacity: 0;
}

.fancybox__thumbs .carousel__slide .fancybox__thumb::after {
    border: 2px solid var(--fancybox-accent-color);
}



:root {
    --fancybox-accent-color: #9C938C;
    --carousel-button-width: 48px;
    --carousel-button-height: 48px;
    --carousel-button-svg-width: 27px;
    --carousel-button-svg-height: 27px;
    --fancybox-bg: rgba(24, 24, 27, 0.92);
    --fancybox-thumbs-width: 96px;
    --fancybox-thumbs-border-radius: 0px;
    --swiper-navigation-color: #fff;

    @media screen and (max-width: $w1440) {
        --swiper-navigation-size: 28px
    }
}

.fancybox__nav {
    @media screen and (max-width: $w580) {
        display: none !important;
    }
}

.grecaptcha-badge {
    display: none !important;
}


/* ================== */

@mixin section-title {
    font-weight: 600;
    font-size: 50px;
    line-height: 140%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #322B2B;

    & span {
        color: #B4A775;
    }

    @media screen and (max-width: $w1440) {
        font-size: 46px;
    }

    @media screen and (max-width: $w1376) {
        font-size: 36px;
    }

    @media screen and (max-width: $w480) {
        font-size: 24px;
    }

    @media screen and (max-width: $w375) {
        font-size: 22px;
    }




}

@mixin section-subtitle {

    font-weight: 600;
    font-size: rem(50);
    line-height: 125%;
    color: #322B2B;

    @media screen and (max-width: $w1376) {
        font-size: 36px;
    }

    @media screen and (max-width: $w1024) {
        font-size: 28px;
    }
}


@mixin section-padding {
    padding: rem(110) 0;

    @media screen and (max-width: $w940) {
        padding: rem(90) 0;
    }

    @media screen and (max-width: $w580) {
        padding: rem(90) 0;
    }
}

.fancybox__container :focus {
    outline: none !important;
}