.checkbox {
    display: flex;
    position: relative;
    cursor: pointer;
    font-size: 14px;
    line-height: 140%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    align-items: flex-start;

    & input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        display: none;
    }

    & .checkbox__text {
        line-height: 140%;
        font-weight: 500;
    }

    & .checkbox__elem {
        top: 0.15em;
        position: relative;
        height: 1em;
        width: 1em;
        min-width: 1em;
        background-color: #fff;
        display: inline-block;
        margin-right: rem(10);

        &::after {
            content: "";
            position: absolute;
            left: 1px;
            top: 2px;
            width: 9px;
            height: 4px;
            transition: 0.25s cubic-bezier(0.08, 0.82, 0.17, 1);
            opacity: 0;
            background-size: 13px;
            background-position: center;
            background-repeat: no-repeat;
            border-left: 2px solid $black;
            border-bottom: 2px solid $black;
            transform: rotate(0);
            background: transparent;
        }
    }

    & input {
        &:hover~.checkbox__elem {
            // border-color: #fff;
        }

        &:checked~.checkbox__elem {
            background-color: #fff;
            z-index: 2;
            // border-color: #fff;

        }

        &:checked~.checkbox__text {
            // color: #fff;
        }

        &:checked~.checkbox__elem:after {
            opacity: 1;
            border-radius: 0;
            transform: rotate(-45deg);
        }
    }

}


/* radio */

.radio {
    display: flex;
    position: relative;
    cursor: pointer;
    font-size: 14px
        /*14*/
    ;
    line-height: 140%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    align-items: flex-start;

    &__title,
    &__desc {
        display: block;
    }

    &__title {
        color: #000;
        font-size: 14px;

    }

    &__desc {
        font-size: 12px;
        color: #959595;
    }

    & input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        display: none;
    }

    &__text {
        line-height: 140%;
    }

    &__elem {
        top: rem(1);
        position: relative;
        height: 18px;
        width: 18px;
        min-width: 18px;
        background-color: none;
        border: 1px solid $grayBlue;
        display: inline-block;
        margin-right: rem(10);
        border-radius: 50%;

        &:after {
            content: "";
            position: absolute;
            left: 3px;
            top: 3px;
            width: 10px;
            height: 10px;
            border: solid $grayBlue;
            border-width: 0 2px 2px 0;
            transition: 0.25s cubic-bezier(.08, .82, .17, 1);
            opacity: 0;
            border-radius: 50%;
        }
    }

    & input {
        &:hover~.radio__elem {
            background-color: transparent;
        }

        &:checked~.radio__elem {
            background-color: transparent;
            z-index: 2;

        }

        &:checked~.radio__elem:after {
            opacity: 1;
            background: $grayBlue;
            border: none;
        }
    }

}

.checkbox-card {

    display: inline-flex;
    cursor: pointer;
    margin-top: 8px;
    margin-right: 8px;

    & input {
        display: none;
    }

    &__text {
        border: 1px solid #2c2c2c;
        padding: 0.2em 0.6em 0.35em 0.6em;
        font-size: 13px;
        border-radius: 5px;
        font-weight: 500;

        &::after {
            content: attr(data-count);
            margin-left: 10px;
            color: $gray;
        }
    }

    & input {
        &:hover~.checkbox-card__text {
            background-color: transparent;
        }

        &:checked~.checkbox-card__text {
            background-color: $grayBlue;
            z-index: 2;
            color: #000;

        }

        &:checked~.checkbox-card__text::after {
            color: #000;
            content: '+';
            font-size: 16px;
            font-weight: bold;
            line-height: 14px;
            transform: rotate(45deg) translateX(1px) translateY(1px);
            display: inline-block;
        }
    }

}

.toggle {
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    color: $main;



    & div {
        display: flex;
        align-items: center;

        & .toggle-slide {
            width: 40px;
            height: 20px;
            display: inline-block;
            background: $green;
            border-radius: 50px 50px;
            position: relative;
            box-shadow: 0 0 0 1px $green;
            margin-right: rem(10);
            transition: 0.3s ease;

            &::before {
                content: '';
                width: 16px;
                height: 16px;
                background: #fff;
                border-radius: 50%;
                position: absolute;
                left: 2px;
                top: 2px;
                display: block;
                transition: 0.3s ease;

            }
        }
    }

    & input {
        display: none;

        &:checked~div .toggle-slide {
            background: #fff;


            &::before {
                left: calc(100% - 18px);
                background: $green;
            }
        }

    }

    & .toggle-active {
        display: none;
    }

    & input:checked~div .toggle-active {
        display: block;
        color: $black;

    }

    & input:checked~div .toggle-deactive {
        display: none;


    }
}